@tailwind base;
@tailwind components;
@tailwind utilities;
body {
    margin: 0;
    font-family: 'Nunito Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #222222;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.25;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #ECECEC;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #006D85;
}


@layer components {
    .form-group {
        @apply mb-6;
    }

    .form-label {
        @apply mb-1 block text-secondary-300 font-semibold;
    }

    .form-input {
        @apply block p-3 w-full border border-secondary-100 rounded-lg focus:border-primary focus:outline-none placeholder-secondary-200 h-12;
    }

    .menu__item {
        @apply hover:text-primary transition ml-8 text-secondary-300 font-semibold relative
    }


    .menu__item_link {
        @apply py-5 inline-block relative before:w-full before:h-2  before:absolute before:-top-1 before:rounded-b-xl
    }

    .menu__item ul li > a.active {
        @apply text-primary bg-[#D9D9D9]
    }

    .menu__item_link.active {
        @apply text-primary  before:bg-primary
    }

    .table {
        @apply w-full  border-separate border-spacing-y-1.5
    }

    .thead .tr {
        @apply border border-b-['#CFD9DB']
    }


    .th {
        @apply py-3.5 px-3  font-semibold uppercase text-secondary-300 text-center whitespace-nowrap min-w-[200px] border-b max-w-[320px]
    }


    .th:first-child {
        @apply text-left
    }

    .td {
        @apply py-3.5 px-3 text-center border-r border-dashed border-r-[#006D85] max-w-[384px]   text-sm
    }

    .td:first-child {
        @apply text-left
    }
    .td:first-child span{
        @apply inline-block
    }
    .td:nth-child(2) {
        @apply whitespace-nowrap
    }

    .td:last-child {
        @apply text-center border-r-transparent
    }

    .tbody .tr {
        @apply bg-white mb-[5px] hover:bg-[#E6F1F3] transition cursor-pointer drop-shadow-tr
    }

    .pagination {
        @apply flex justify-end;
    }

    .pagination li {
        @apply text-secondary-300 p-1 w-10 h-10 text-center rounded-lg  flex justify-center items-center border border-transparent font-semibold;
    }

    .pagination li.selected {
        @apply border-primary text-primary;
    }
}